import React from 'react';
import { Link , NavLink} from 'react-router-dom';
import {Button, Col, Row, Container, Navbar, NavDropdown, NavItem, Nav} from 'react-bootstrap';
import SignOutButton from '../SignOut';
import * as ROUTES from '../../constants/routes';

import { AuthUserContext } from '../Session';
const pageStyle = {
  border:"2px white solid",
  backgroundColor:"rgb(66, 66, 69, .8)",
  borderRadius:"20px",
  boxShadow:"5px 5px 5px black",
}
const buttonStyle = {
  border:"1px white solid"
}

const Navigation = () => (
  <div>
    <AuthUserContext.Consumer>
      {authUser =>
        authUser ? <NavigationAuth /> : <NavigationNonAuth />
      }
    </AuthUserContext.Consumer>
  </div>
);

const NavigationAuth = () => (

 <Navbar className = "fixed-top color-nav" expand="lg">
    <Navbar.Brand href="#home">Authentication</Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="mr-auto">
        <NavLink className = "mr-3" to={ROUTES.LANDING}><Button block variant = "primary">Landing</Button></NavLink>
        <NavLink className = "mt-2 mr-3" to={ROUTES.HOME}><Button block variant = "secondary">Home</Button></NavLink>
        <NavLink className = " mt-2 mr-3" to={ROUTES.ACCOUNT}><Button block variant="info">Account</Button></NavLink>
        <SignOutButton />
      </Nav>
    </Navbar.Collapse>
  </Navbar>

);

const NavigationNonAuth = () => (
    <Navbar className = " fixed-top color-nav" expand="lg">
      <Navbar.Brand href="#home">Welcome!</Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="mr-auto">
          <NavLink className = "mr-3 ml-3" to={ROUTES.LANDING}><Button block variant = "primary">Landing</Button></NavLink>
          <NavLink className = "mr-3 ml-3" to={ROUTES.SIGN_IN}><Button block variant = "secondary">Sign In</Button></NavLink>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
);

export default Navigation;